<template>
  <div class="home center" ref="grid">
    <router-link
      tag="div"
      :to="{
        path: `/posts/${titles[i]}`,
        query: {
          bg: backgrounds[i]
        }}"
      @click.native="animate()"
      class="block"
      v-for="(post, i) in postCount"
      :key="`post-${i}`"
      :style="getStyle(i)"
      :id="`block-${i}`"
    >
      <transition name="router" mode="out-in">
        <div class="excerpt" :key="excerptKey">
          <h3 class="sub-title"></h3>
          <h2 class="title">{{ titles[i] || 'Ommen' }}</h2>
        </div>
      </transition>
    </router-link>
  </div>
</template>

<script>
import Path from "@/hampath";

export default {
  name: "Home",
  data() {
    return {
      postCount: 20,
      posts: [],
      isShowing: 0,
      init: true,
      path: [],
      titles: [
        "Ommen",
        "Emmen",
        "Hardenberg",
        "Groningen",
        "Utrecht",
        "Almelo",
        "Enschede",
        "Amsterdam",
        "Eindhoven",
        "Leeuwarden",
        "Meppel",
        "Winschoten",
        "Zwolle",
        "Gieten",
        "Assen",
        "Tilburg",
        "Maassluis",
        "Hengelo",
        "Slagharen",
        "Coevorden"
      ],
      backgrounds: [],
      excerptKey: 0
    };
  },
  mounted() {
    this.path = Path.prepare(10, 10);
    // this.$root.$emit("white", true);
    this.$nextTick(this.pinContainerScene);
  },
  beforeRouteLeave(to, from, next) {
    var vm = this;
    this.$nextTick(vm.destroy());
    next();
  },
  methods: {
    getStyle(i) {
      let counter = i;
      let imgUrl =
        `https://source.unsplash.com/800x6${i < 10 ? "0" : ""}` +
        `${counter}/?people`;
      if (this.path[i]) {
        this.backgrounds[i] = imgUrl;
        return {
          backgroundImage: `url(${imgUrl})`,
          ...this.path[i][i].from
        };
      }
    },
    animate() {
      console.log("animate");
      this.excerptKey = 1;
    },
    pinContainerScene() {
      const Length = this.postCount;

      const tl = new this.$gsap.TimelineMax();
      for (var i = 0; i < Length; i++) {
        tl.to(`#block-${i}`, 1, {
          ease: this.$gsap.Linear.easeNone,
          ...this.path[i][i].to,
          delay: 1
        });
        tl.to(
          `#block-${i + 1}`,
          1,
          {
            ease: this.$gsap.Linear.easeNone,
            top: 0,
            left: 0
          },
          "-=1"
        );
      }
      const scene = new this.$scrollmagic.Scene({
        triggerElement: "#app",
        triggerHook: "onLeave",
        duration: `${Length * 100}%`
      })
        .setPin("#app")
        .setTween(tl);

      this.$ksvuescr.$emit("addScene", "pinContainerScene", scene);
    },
    destroy() {
      this.$ksvuescr.$emit("destroy");
    }
  }
};
</script>

