<template>
  <div class="main-menu center" :class="{ active: menuToggle, light: lightMenu }">
    <div class="outer center" @click="toggleMenu">
      <div class="inner">
        <!-- <div class="inner-inner"></div> -->
      </div>
    </div>
    <transition-group
      appear
      tag="ul"
      class="menu"
      v-if="menuToggle"
      name="staggered-fade"
      v-bind:css="false"
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:leave="leave"
    >
      <router-link
        v-for="(item, index) in computedList"
        :to="item.url"
        tag="li"
        v-bind:key="item.link"
        v-bind:data-index="index"
        @click.native="toggleMenu()"
      >{{item.link}}</router-link>
    </transition-group>
  </div>
</template>

// <script src="https://cdnjs.cloudflare.com/ajax/libs/velocity/1.2.3/velocity.min.js"></script>
<script>
import Velocity from "velocity-animate";

export default {
  name: "Menu",
  mounted() {
    this.$root.$on("white", this.toggleLightMenu);
  },
  data() {
    return {
      menuToggle: false,
      lightMenu: false,
      computedList: [
        {
          link: "de buurten",
          url: "/posts"
        },
        {
          link: "waar we voor staan",
          url: "/posts"
        },
        {
          link: "de buurtontdekkers",
          url: "/"
        },
        {
          link: "contact",
          url: "/contact"
        }
      ]
    };
  },
  methods: {
    toggleLightMenu() {
      this.lightMenu = !this.lightMenu;
    },
    toggleMenu() {
      this.menuToggle = !this.menuToggle;
      this.$root.$emit("toggle", this.menuToggle);
    },
    beforeEnter: function(el) {
      el.style.opacity = 0;
      el.style.height = 0;
      el.style.left = "100%";
    },
    enter: function(el, done) {
      var delay = el.dataset.index * 250;
      setTimeout(function() {
        Velocity(
          el,
          { opacity: 1, height: "1.6em", left: 0 },
          { complete: done }
        );
      }, delay);
    },
    leave: function(el, done) {
      var delay = el.dataset.index * 150;
      setTimeout(function() {
        Velocity(el, { opacity: 0, height: 0 }, { complete: done });
      }, delay);
    }
  }
};
</script>

<style lang="scss" scoped>
