        border-width: 0;
        border-color: #fff;
        border-left-width: var(--border-width-xs);
        transform-origin: 10px 38px;
        transition: transform 0.3s ease-in;
        transform: rotate(45deg);

        &:before {
          content: "";
          height: 4vmin;
          width: 4vmin;
          position: absolute;
          right: 0;
          top: 0;
          left: 0;
          bottom: 0;
          border-right: var(--border-width-xs) solid #fff;
          transform-origin: 17px 38px;
          transform: rotate(-90deg);
          display: flex;
          z-index: 2;
        }
      }
    }
  }

  .outer {
    width: calc(3vmin - 4px);
    height: calc(3vmin - 4px);
    border: var(--border-width-xs) solid #000;
    cursor: pointer;
  }

  .inner {
    display: flex;
    width: calc(1.1vmin - 5px);
    height: calc(1.1vmin - 5px);
    border: var(--border-width-xs) solid #000;
    cursor: pointer;

    &:before {
      transform: rotate(90deg);
    }
  }

  .menu {
    color: #fff;
    font-size: var(--font-xxl);
